import React, { useState, useEffect, useRef, useCallback } from 'react';

/**
 * 
 * @param {Object} gameState - 
 * @param {Object} effectMonitor - EffectMonitor ()
 * @returns {Object} 
 */
const useEnhancedLog = (gameState, effectMonitor) => {
    const [isEnabled, setIsEnabled] = useState(true); // /
    const effectMonitorRef = useRef(effectMonitor); // EffectMonitor ()

    // /
    useEffect(() => {
        // 
        // animationCallbacks
        // (App.jspresentationControllereffectMonitor)
        // onEffectProcessingComplete

        // 
        return () => {
            // effectMonitor.removeEffectProcessingCallback 
        };
    }, [effectMonitor]); // effectMonitor

    /**
     *  (gameState.game_log)
     */
    const clearLog = useCallback(() => {
        // initializeGamegameState.game_log
        // App.jsdispatch
        // gameState
        //console.log('DEBUG: clearLog called. Log state is reset via initializeGame or external dispatch.');
    }, []);

    /**
     * /
     */
    const toggleEnabled = useCallback(() => {
        setIsEnabled(prev => !prev);
    }, []);

    //  (gameState.game_log)
    useEffect(() => {
        if (!gameState) {
            return;
        }

        //  (gameState.game_log)
        // game_loginitializeGame
        // 
    }, [gameState?.round_number]);

    return React.useMemo(() => {
        // game_log
        const gameLog = gameState?.game_log || [];

        /**
         *  ()
         */
        const getCombinedLog = () => {
            // game_logGameLogOverlay
            return [...gameLog];
        };

        /**
         * 
         */
        const getFilteredEntries = (filterType = 'all') => {
            if (!isEnabled) {
                return [];
            }
            const combinedLog = getCombinedLog();
            
            if (filterType === 'all') {
                return combinedLog;
            }

            return combinedLog.filter(entry => {
                // game_log
                if (entry.source === 'game_log') { // 'effect_queue'
                    return true;
                }
                // LogEntryGeneratoreffectType
                switch (filterType) {
                    case 'card_play':
                        return entry.effectType === 'PLAYER_ACTION' || 
                               (entry.description && (entry.description.includes('') || entry.description.includes('')));
                    case 'damage':
                        return entry.effectType === 'CARD_DURABILITY_CHANGED' || // 
                               (entry.description && (entry.description.includes('') || entry.description.includes('')));
                    case 'resource':
                        return entry.effectType === 'CONSCIOUSNESS_CHANGED' ||
                               entry.effectType === 'SCALE_CHANGED' ||
                               entry.effectType === 'SET_CONSCIOUSNESS' ||
                               entry.effectType === 'SET_SCALE' ||
                               (entry.description && (entry.description.includes('') || entry.description.includes('')));
                    case 'card_move':
                        return entry.effectType === 'MOVE_CARD' ||
                               (entry.description && (entry.description.includes('') || entry.description.includes('')));

                    default:
                        return true;
                }
            });
        };

        /**
         * 
         */
        const getLogStats = () => {
            const currentLog = getCombinedLog();
            const effectLogCount = currentLog.filter(entry => entry.source === 'effect_queue').length;
            const progressLogCount = currentLog.filter(entry => entry.source === 'game_log').length; // game_logEffectMonitorEffectQueue
            
            return {
                total: currentLog.length,
                effect: effectLogCount,
                progress: progressLogCount, // progressgame_logEffectQueueeffect
                isEnabled
            };
        };

        return {
            //  (enhancedEntries)
            combinedLog: getCombinedLog(),
            
            // 
            getFilteredEntries,
            
            // 
            clearLog, // no-op
            toggleEnabled,
            isEnabled,
            
            // 
            getLogStats,
            
            // 
            getEffectMonitor: () => effectMonitorRef.current
        };
    }, [gameState?.game_log, isEnabled, gameState?.round_number, effectMonitor]); // gameState.game_log
};

export default useEnhancedLog;